import Logger from "../../../clients/logger";

import { WindowPrimitives as FinsembleWindowPrimitivesEntry } from "./windowPrimitivesEntryPoints";
import { WindowCreateEntry as FinsembleWindowCreateEntry } from "./windowCreateEntryPoints";
import { TabbingEntry as FinsembleTabbingEntry } from "./tabbingEntryPoints";
import { LauncherEntry as FinsembleLauncherEntry } from "./launcherEntryPoints";
import { series as asyncSeries } from "async";

export class ServiceEntryPoints {
	constructor(manifest, dockingMain, launcher, stackedWindowManager) {
		this.finsembleLauncherEntry = new FinsembleLauncherEntry(manifest, launcher);
		this.finsembleWindowPrimitivesEntry = new FinsembleWindowPrimitivesEntry(dockingMain);
		this.finsembleWindowCreateEntry = new FinsembleWindowCreateEntry(manifest, launcher);
		this.finsembleTabbingEntry = new FinsembleTabbingEntry(stackedWindowManager);
		this.bindFunctions();
	}

	bindFunctions() {
		this.initialize = this.initialize.bind(this);
		this.shutdown = this.shutdown.bind(this);
	}

	initialize(done) {
		Logger.system.debug("ServiceEntryPoints.initialize start");
		asyncSeries([
			(finish) => { this.finsembleLauncherEntry.initialize(finish); },
			(finish) => { this.finsembleWindowPrimitivesEntry.initialize(finish); },
			(finish) => { this.finsembleWindowCreateEntry.initialize(finish); },
			(finish) => { this.finsembleTabbingEntry.initialize(finish); }
		], () => {
			Logger.system.debug("ServiceEntryPoints.initialize done");
			done();
		});
	}

	shutdown(done) {
		asyncSeries([
			(finish) => { this.finsembleLauncherEntry.shutdown(finish); },
			(finish) => { this.finsembleWindowPrimitivesEntry.shutdown(finish); },
			(finish) => { this.finsembleWindowCreateEntry.shutdown(finish); },
			(finish) => { this.finsembleTabbingEntry.shutdown(finish); }
		], () => {
			Logger.system.debug("ServiceEntryPoints.shutdown done");
			done();
		});
	}
}



