import { BaseService } from "../baseService";
import RouterClient from "../../clients/routerClientInstance";
import { series as asyncSeries } from "async";
import FSBLDepdendencyManager from "../../common/dependencyManager";
import DistributedStoreClient from "../../clients/distributedStoreClient";
import DockingMain from "./Docking/dockingMain";
import { Launcher } from "./Launcher/launcher";
import { ServiceEntryPoints as SetupAllEntryPoints } from "./ServiceEntryPoints/setupAllEntryPoints";
import StackedWindowManager from "./StackedWindowManager/stackedWindowManager";

DistributedStoreClient.initialize();

import ConfigClient from "../../clients/configClient";
ConfigClient.initialize();

import Logger from "../../clients/logger";
Logger.start();

class WindowService extends BaseService {
	constructor(params) {
		params.name = "windowService";
		super(params);
		this.params = params;
		this.doShutdown = this.doShutdown.bind(this);
		this.onShutdown(this.doShutdown);
	}

	initialize(manifest, callback) {
		Logger.system.debug("WindowService.initialize start");

		window.RouterClient = RouterClient;

		window.stackedWindowManager = StackedWindowManager;
		window.stackedWindowManager.initialize(manifest.finsemble); // pass in available config so stacked window has on startup (without having to query)

		let dockingMain = new DockingMain(this.params, {
			Logger
		});
		window.DockingMain = dockingMain;

		let launcher = new Launcher(manifest, stackedWindowManager);
		window.launcher = launcher;

		// setup external interfaces (i.e. the API supported by the window service)
		let setupAllEntryPoints = new SetupAllEntryPoints(manifest, dockingMain, launcher, window.stackedWindowManager);
		window.setupAllEntryPoints = setupAllEntryPoints;

		// an ordered initialization -- on completion the window service is ready
		asyncSeries([
			(done) => { dockingMain.initialize(done); },
			(done) => { launcher.initialize(done); },
			(done) => { setupAllEntryPoints.initialize(done); }
		], callback);
	}

	// This method handles the shutdown sequence for the launcherService. TODO: this currently doesn't finish preventing the service from shutting down
	doShutdown() {
		const promiseResolver = (resolve) => {
			asyncSeries([
				(done) => { window.setupAllEntryPoints.shutdown(done); },
				(done) => { window.launcher.shutdown(done); },
				(done) => { window.setupAllEntryPoints.shutdown(done); },
				resolve
			]);
		};
		return new Promise(promiseResolver);
	}
}

var serviceInstance = new WindowService({
	name: "windowService",
	startupDependencies: {
		services: [],
		clients: ["configClient", "distributedStoreClient"]
	},
	addOFWrapper: false
});

serviceInstance.onBaseServiceReady(function (callback) {
	ConfigClient.getValues(null, (err, manifest) => {
		serviceInstance.initialize(manifest, callback);
	});
});

window.WindowService = serviceInstance;
serviceInstance.start();

export default serviceInstance;



