import { BaseWindow } from "./BaseWindow";
import OpenFinWindow from "./openfinWindowWrapper";
import NativeWindow from "./externalWindowWrapper";
import StackedWindow from "./stackedWindowWrapper";

BaseWindow.registerType("OpenFinWindow", OpenFinWindow);
BaseWindow.registerType("NativeWindow", NativeWindow);
BaseWindow.registerType("StackedWindow", StackedWindow);

export const FinsembleWindowInternal = BaseWindow;



